.container {
    width: 75%;
    height: 50%;
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    vertical-align: middle;
}

.logoDiv {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: -25%;
    width: 17.5vh;
    height: 17.5vh;
    background-color: rgb(56, 63, 75, 1);
    border: .25vh solid #222a37;
    border-radius: 15vh;
}

.logoDiv:hover {
    background-color: rgb( 9, 132, 227, .75);
    animation: content_pulse 2s infinite;
    cursor: pointer;
}

.logoDiv .logo {
    margin-bottom: 1vh;
}

.loginDiv {
    width: 25vw;
    height: 50vh;
    border-radius: 5vh;
    background-color: #383f4b;
    border: .25vh solid #222a37;
    padding-top: 10vh;
}

.text-input {
    display: block;
    margin-top: 2vh;
    width: auto;
    text-align: center;
    padding: 1vh;
}

.text-input .text-label{
    float: left;
    margin-left: 1vh;
    margin-bottom: 1vh;
    text-align: left;
    font-size: 2vh;
    color: white

}

.text-input input {
    align-self: center;
    font-size: 2vh;
    width: 90%;
    padding: 1vh;
}

.loginDiv-sub {
    position: absolute;
    width: 20vw;
    height: 25vh;
    margin-top: 40vh;
    border-radius: 5vh;

}

.loginDiv-sub-top {
    position: absolute;
    top: 0vh;
    width: 100%;
    height: auto;
    background-color: rgb(56, 63, 75, .9);
    border: .25vh solid #222a37;
    border-top-right-radius: 5vh;
    border-top-left-radius: 5vh;
    text-align: center;
    border-bottom: none;
    
}


.loginDiv-sub-top a{
    color: #7a7f87;
    text-decoration: underline;
    font-size: 2vh;
}

.loginDiv-sub-top a:hover {
    color: #0984e3;
    cursor: pointer;
}

button {
    font-family: 'Epilogue', sans-serif;
    font-weight: 600;
    color: white;
    margin: 1vh;
    font-size: 2vh;
    width: 50%;
    height: 4vh;
    text-align: center;
    border-radius: 1vh;
    border: .1vh solid #222a37;

}

button:hover {
    transform: scale(1.15); 
    transition: ease-in-out;
    animation: content_pulse 2s infinite;
    cursor: pointer;
}

.log-in-btn {
    background-color: #0984e3
}

.forgot-pass-btn {
    background-color: #054f88;
}

@keyframes content_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
        border: .1vh white;
	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}